<template>
  <div>
    <el-radio-group v-model="listType">
      <el-radio label="列表"></el-radio>
      <el-radio label="平铺"></el-radio>
    </el-radio-group>
    <el-table
      v-if="listType === '列表'"
      :data="tableData"
      :cell-style="{ 'text-align': 'center' }"
      :header-cell-style="{ 'text-align': 'center' }"
      style="width: 100%"
    >
      <el-table-column type="index"> </el-table-column>
      <el-table-column prop="file_name" label="文件名" width="180px">
      </el-table-column>
      <el-table-column
        prop="size"
        label="文件大小"
        width="180px"
        :formatter="dealSize"
      >
      </el-table-column>
      <el-table-column
        prop="upload_time"
        label="上传时间"
        width="180px"
        :formatter="dealTime"
      >
      </el-table-column>
      <el-table-column prop="download" label="下载次数" width="180px">
      </el-table-column>
      <el-table-column prop="type" label="类型" width="180px">
      </el-table-column>
      <el-table-column label="操作" min-width="200px">
        <template slot-scope="scope">
          <a :href="getFile(scope.row)" :download="scope.row.file_name">
            <el-button size="mini" type="success" @click="handleDownload"
              >下载</el-button
            >
          </a>
          <el-button
            size="mini"
            type="primary"
            @click="handleShare(scope.$index, scope.row)"
            >分享</el-button
          >
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <div class="listpp" v-if="listType === '平铺'">
      <div class="infoBox outside">
        <div class="infoBar outside" v-if="barFlag === 1">
          <p class="fs outside">
            文件大小：
            {{ clickFile.size }}
          </p>
          <p class="ft outside">
            上传时间：
            {{ clickFile.upload_time }}
          </p>
          <p class="fd outside">
            下载次数：
            {{ clickFile.download }}
          </p>
          <div class="fb outside">
            <el-button type="success" size="mini">下载</el-button>
            <el-button type="primary" size="mini">分享</el-button>
            <el-button type="danger" size="mini">删除</el-button>
          </div>
        </div>
      </div>
      <div
        :class="{ fileListpp: true, fileListppClick: item.id === clickFile.id }"
        v-for="item in tableData"
        :key="item.id"
        @click="infoBar(item)"
        v-clickoutside:outside="closebar"
      >
        <div class="fileIcon outside">
          <img
            class="icon"
            width="256"
            height="256"
            :src="
              require('../assets/images/' + item.type.replace(/./, '') + '.png')
            "
            :alt="item.file_name"
          />
        </div>
        <p class="fileName">{{ item.file_name }}</p>
      </div>
    </div>
  </div>
</template>  

<script>
import Clickoutside from "element-ui/src/utils/clickoutside";
export default {
  directives: { Clickoutside },
  data() {
    return {
      tableData: [],
      listType: "平铺",
      barFlag: 2,
      clickFile: {
        file_name: "",
        size: "",
        upload_time: "",
        download: "",
        downloadUrl: "",
        id: 0,
      },
    };
  },
  methods: {
    closebar() {
      this.barFlag = 2;
      this.clickFile = {
        file_name: "",
        size: "",
        upload_time: "",
        download: "",
        downloadUrl: "",
        id: 0,
      };
    },
    infoBar(item) {
      this.barFlag = 1;
      console.log(this.barFlag);
      this.clickFile.file_name = item.file_name;
      this.clickFile.size = this.dealSize({ size: item.size * 1 });
      this.clickFile.upload_time = item.upload_time;
      this.clickFile.download = item.download;
      this.clickFile.id = item.id;
      this.clickFile.downloadUrl = `http://accurate.uicp.net:8081/file/download/${item.id}/${item.hash_name}`;
    },
    handleDelete(index, row) {
      this.$http
        .delete(`/file/delete/${row.hash_name}/${row.id}`)
        .then((res) => {
          this.$message.success(res.data.msg);
          this.refreshFileList();
        })
        .catch((err) => {
          console.log("Error=>", err);
        });
    },
    handleShare(index, row) {
      let url = `http://accurate.uicp.net:8081/file/download/${row.id}/${row.hash_name}`;
      this.$alert(
        `<p style="word-wrap: break-word; word-break: break-all;">${url}</p>`,
        "复制以下链接分享给好友哦~",
        {
          dangerouslyUseHTMLString: true,
          confirmButtonText: "确定",
          callback: (action) => {
            // this.$message({
            //   type: "info",
            //   message: `action: ${action}`,
            // });
          },
        }
      );
    },
    handleDownload() {
      setTimeout(() => {
        this.refreshFileList();
      }, 1000);
    },
    refreshFileList() {
      this.getFileList();
    },
    getFileList() {
      let params = {
        uid: sessionStorage.getItem("uid"),
      };
      this.$http
        .post("/file/list", params)
        .then((res) => {
          if (res.data.code === 0) {
            this.$message.error(res.data.msg);
          } else {
            this.tableData = res.data;
            console.log(this.tableData);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getFile(data) {
      let url = `http://accurate.uicp.net:8081/file/download/${data.id}/${data.hash_name}`;
      return url;
    },
    dealSize(row, column) {
      let fileSize = (row.size / 1024).toFixed(2);
      return `${fileSize}kb`;
    },
    dealTime(row, column) {
      return this.formatTime(row.upload_time);
    },
    formatTime(value) {
      var date = new Date(value);
      var Y = date.getFullYear();
      var M =
        date.getMonth() + 1 < 10
          ? `0${date.getMonth() + 1}`
          : date.getMonth() + 1;
      var D = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate();
      var h = date.getHours() < 10 ? `0${date.getHours()}` : date.getHours();
      var m =
        date.getMinutes() < 10 ? `0${date.getMinutes()}` : date.getMinutes();
      var s =
        date.getSeconds() < 10 ? `0${date.getSeconds()}` : date.getSeconds();
      return `${Y}-${M}-${D} ${h}:${m}:${s}`;
    },
  },
  mounted() {
    this.getFileList();
  },
};
</script>
<style scoped>
.el-button + .el-button {
  margin: 0;
}
.listpp {
  width: 100%;
  overflow: hidden;
}
.fileListpp {
  float: left;
  width: 158px;
  height: 200px;
  font-size: 16px;
}
.fileListppClick {
  box-sizing: border-box;
  background: #daf5ff;
  border: 1px solid #1ed0ff;
  border-radius: 10px;
}
.fileIcon {
  width: 158px;
  height: 143px;
}
.icon {
  margin: 19px 19px 0 19px;
  width: 120px;
  height: 120px;
}
.infoBox {
  width: 100%;
  height: 50px;
}
.infoBar {
  width: 100%;
  height: 50px;
}
.infoBar p {
  display: block;
  font-size: 20px;
  text-align: left;
  line-height: 50px;
  float: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.infoBar .ft {
  width: 380px;
}
.infoBar .fs {
  width: 270px;
}
.infoBar .fd {
  width: 140px;
}
.infoBar .fb {
  padding-top: 10px;
  width: 200px;
  float: left;
}
</style>



